/* Get-involved page */

.page-id-20 {
  // Get-involved Global Styles

  letter-spacing: 0.1rem;

  h3 {
    font-family: $font__heading3;
    margin-bottom: 0;
    margin-left: 0;
    font-weight: 300;
    width: 100%;
    margin-top: 2rem;
  }

  h4 {
    font-family: $font__heading4;
    font-weight: 300;
    line-height: 1.75rem;
  }

  li {
    margin-bottom: 0.75rem;
  }

  // Landing Section

  .landing {
    h1 {
      margin-top: 15rem;

      @include desktop {
        margin-top: 8rem;
      }
    }

    h3 {
      font-size: 1.5rem;
      width: 40%;
      font-family: $font__hero-image-sub-heading-description;
      font-weight: 300;
      margin-top: 0;
      line-height: 3rem;
      justify-content: center;
    }
  }

  .landing-title {
    font-size: 3rem;
  }

  .landing-description {
    display: none;

    @include desktop {
      display: inline;
    }
  }

  .landing {
    i {
      font-size: 5rem;
      opacity: 0.5;
      color: $white;

      @include desktop {
        display: none;
      }
    }
  }

  // Upcoming Events Section

  .upcoming-events-content {
    display: flex;
    align-content: center;
    justify-content: center;

    @include desktop {
      flex-direction: row;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      width: 1200px;
    }
  }

  .events-title {
    width: 100%;
    text-align: center;

    @include desktop {
      width: 80%;
      text-align: left;
    }
  }

  .upcoming-events {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include desktop {
      flex-direction: row;
    }

    .event-wallpaper {
      position: relative;
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
      height: 350px;
      background-size: 664px 1022px;
      border-radius: 5px;
      margin: 1.5rem;
      width: 330px;
      color: $white;

      @include desktop {
        width: 300px;
        flex-wrap: nowrap;
      }

      .upcoming-date {
        background-color: $lighter_green;
        border: $light_green;
        box-shadow: 0 4px 4px rgba(12, 11, 11, 0.1);
        border-radius: 50%;
        position: absolute;
        top: 10px;
        right: 80%;
        padding: 15px 22px;
        margin: 0;

        @include desktop {
          right: 255px;
        }

        .bold-date {
          font-weight: 600;
          font-family: $font__button;
          font-size: 1.5rem;
          margin: 0;
          padding: 0;
          text-align: center;
        }

        .upper-month {
          text-transform: uppercase;
          font-family: $font__body;
          font-weight: 300;
          margin: 0;
          padding: 0;
          text-align: center;
        }
      }
    }

    .event-description-1 {
      font-family: $font__heading3;
      text-align: flex-start;
      margin-left: 1.5rem;
      align-content: center;
      margin-bottom: 3rem;
      font-size: 1.5rem;

      @include desktop {
        margin-bottom: 1.75rem;
      }
    }

    .event-description {
      font-family: $font__heading3;
      text-align: flex-start;
      margin-left: 1.5rem;
      align-content: center;
      margin-bottom: 2rem;
      font-size: 1.5rem;
    }

    .learn-btn-events {
      padding: 12px 45px;
      letter-spacing: 0.1rem;
      font-weight: 500;
      font-family: $font__hero-image-sub-heading-description;
      font-size: 1.1rem;
      border: $light_green;
      align-self: flex-start;
      text-decoration: none;
      color: $white;
      margin-bottom: 1rem;
      margin-left: 0;
      padding-left: 1.5rem;
      i {
        margin-left: 10px;
        font-size: 18px;
      }
    }
  }

  // Become a member Section

  .become-a-member {
    h3 {
      text-align: left;
      padding: 1rem;
      font-size: 1.9rem;
    }
  }

  .member-description {
    font-family: $font__body;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include desktop {
      flex-direction: row;
    }

    .left-half {
      padding: 1rem;
      display: none;

      @include desktop {
        display: block;
        flex-basis: 50%;
      }
    }

    .right-half {
      padding: 1rem;
      flex-basis: 100%;
      margin-bottom: 3rem;

      @include desktop {
        flex-basis: 50%;
        margin-bottom: 0;
      }
    }

    p {
      line-height: 1.75rem;
      margin-bottom: 0;
    }

    ul {
      padding: 1rem;
    }

    a {
      padding: 12px 25px;
      letter-spacing: 0.1rem;
      font-weight: 500;
      font-family: $font__hero-image-sub-heading-description;
      font-size: 1rem;
      color: $light_green;
      background-color: transparent;
      border: $light_green;
      border: solid $light_green 2px;
      border-radius: 5px;
      text-decoration: none;
      align-self: inherit;

      i {
        margin-left: 10px;
        font-size: 15px;
      }
    }

    h4 {
      padding-top: 1rem;
      border-top: solid $grey 1px;
      width: 100%;
      margin-bottom: 0;
    }

    .standard-class {
      margin-top: 1rem;
    }

    .voting-class-title {
      margin-top: 2.5rem;
    }
  }

  // Support Our Cause Section

  .support-our-cause {
    line-height: 1.75rem;
    p {
      margin-top: 0.3rem;
    }
    @include desktop {
      width: 40%;
    }

    h3 {
      font-size: 2rem;
    }
  }
}
